$color-main: #4054E0;
$color-front: rgba(255,255,255,1);
$color-front-light: rgba($color-front, 0.4);
$color-front-very-light: rgba($color-front, 0.2);

$font-main: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;

body {
    background-color: $color-main;
    font-family: $font-main;
    font-size: 20px;
    color: #fff;
    padding: 3rem;
}

a {
    color: $color-front;
}

.logo {
    width: 6rem;
}

p {

    color: $color-front-light;

    em {
        font-style: normal;
        color: $color-front;
    }

    a {
        text-decoration: none;
    }

    a:hover {
        text-decoration: underline;
    }

}

footer {
    margin-top: 2rem;
    color: $color-front-very-light;
    font-size: .8rem;

}


// FORMS

.form-group,
.form-control {
    display: block;
}

input,
textarea {
    font-family: $font-main;
    padding: 0.3rem;
}